<template>
  <div class="loginWrap">
    <div class="loginInfo">
      <div class="loginLogo">王新龙个人博客</div>
      <div class="loginInput">
        <div>
          <label>
            <span>登录名:</span>
            <input type="text" v-model="loginName" />
          </label>
        </div>
        <div>
          <span> 密码:</span>
          <input type="password" v-model="password" />
        </div>
          <div>
          <span>验证码:</span>
          <input type="text" v-model="vcode" />
        </div>

      </div>
               <div class="vcode">
          <img v-on:click="changeVCodeUrl" id="imgCaptcha" v-bind:src="vCodeUrl" alt />
      </div>
      <div class="loginButton">
        <button @click="handleClick">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginName: null,
      password: null,
      vCodeUrl: '/api/verificationCode',
      vcode: null,
    };
  },
  methods: {
    openMessage(messageType, message) {
      this.$message({
        message,
        type: messageType, // success/warning/info/error
      });
    },
    async handleClick() {
      // 验证属性
      if (!this.loginName) {
        this.openMessage('warning', '登录不能为空');
        return;
      }
      if (!this.password) {
        this.openMessage('warning', '密码不能为空');
        return;
      }
      if (!this.vcode) {
        this.openMessage('warning', '验证码不能为空');
        return;
      }
      const result = await this.$store.dispatch('loginUser/login', {
        loginName: this.loginName,
        password: this.password,
        verificationCode: this.vcode,
      });
      if (+result.code === 0) {
        this.$store.commit('loginUser/setData', result.data);
        this.$router.push('/management');
      } else {
        this.openMessage('error', result.msg);
      }
    },
    changeVCodeUrl() {
      this.vCodeUrl = `/api/verificationCode?${new Date().getTime()}`;
    },
  },

};
</script>

<style lang="scss" scoped>
.loginWrap {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  color: #fff;
  background-color: coral;
  background-image: url("../assets/bg2.jpg");
  background-size: 100% 100%;
  .loginInfo {
    position: absolute;
    width: 444px;

    background-color: rgba(0, 0, 0, 0.5);
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .loginLogo {
      width: 100%;
      height: 80px;
      line-height: 80px;
      text-align: center;
      font-size: 20px;
    }
     .vcode{
        width: 324px;
        height: 34px;
          margin: 0 auto;
      }
    .loginInput {
      width: 100%;
      height: 210px;

      div {
        position: relative;
        width: 324px;
        height: 34px;
        padding-top: 30px;
        margin: 0 auto;
        border-bottom: 1px solid #fff;
        input {
          width: 100%;
          height: 100%;
          padding-top: 30px;
          border: none;
          outline: none;
          background: none;
          padding: 0 10px 0 60px;
          box-sizing: border-box;
          color: #fff;
        }
        span {
          position: absolute;
          height: 30px;
          line-height: 30px;
          width: 55px;
          /* text-align: center; */
          /* border: 1px solid red; */
        }
      }
    }
    .loginButton {
      width: 100%;
      height: 102px;
      line-height: 102px;
      text-align: center;
      button {
        display: inline-block;
        width: 324px;
        height: 45px;
        color: rgb(186, 205, 224);
        background: rgb(11, 26, 50, 0.7);
        border-radius: 3px;
        font-size: 18px;
      }
    }
  }
}
</style>
